.App {
  text-align: center;
  font-family: Roboto;

}

p {
  font-size: calc(10px + 1em);

}



/* .App h1{
    font-size: calc(10px + 0.8em);
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;

}

.About-logo {
  height: 25vmin;
  pointer-events: none;
  padding: 0px;
}

.MyButton { color:white;}

.Box { display:inline-block; margin: 10px;}


.Music {
  background: #659999;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #f4791f, #659999);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #f4791f, #659999); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: white;
}

.Bio {
  text-align: left;
  padding: 0;
}
.Contact {
  background: #FF8008;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFC837, #FF8008);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FFC837, #FF8008); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #fffff;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 0.8em);
  color: black;
}
.App-header p{
   margin: 0
}

.App-header h1{
   margin: 0
}


.App-link {
  color: #61dafb;
}
